import React from 'react'
import Header from '../components/Header'
import Footer from '../components/Footer'
export default function Design2() {
    return (
     <div>
         <Header/>
  {/*body content wrap start*/}
  <div className="main">
    {/*hero section start*/}
    <section className="hero-section ptb-100 sample" >
      <div className="container">
        <div className="row align-items-center justify-content-center">
          <div className="col-md-9 col-lg-7">
            <div className="page-header-content text-white text-center pt-sm-5 pt-md-5 pt-lg-0">
              <h2 className="text-white mb-0">Free Samples of Design
              </h2>
              <p><i className="fa fa-leaf" /><b>Free Tumitin Report</b></p>
              <p><i className="fa fa-flag" /><b>7,00000+ Total Satisfied Student</b></p>
              <p><i className="fa fa-thumbs-up" /><b>150+ Executive for Support Service</b></p>
            </div>
          </div>
        </div>
      </div>
    </section>
    {/*hero section end*/}
    <br />
    <br />
    <br />
    <div className="container">
      <h3 className="text-center ico" style={{borderBottom: '12px solid  black'}}>Report on Game Design
      </h3>
      <h4>Introduction
      </h4>
      <p>Game development may be as a process of designing and introducing the creative aspects which entertain
        people to the large extent. Now, gaming industry is facing high level of competition which have major
        impact on firm’s profitability which are operating in such sector. In this, report will provide deeper
        insight about the extent to which mobile and tablet devices have influenced or encouraged everyone to
        play game. This report will also develop understanding about the games which attract large number along
        with the significant or valid reason.
      </p>
      <h5>Background of the study
      </h5>
      <p>In the present times, video game industry is growing with the very high pace and places high level of
        emphasis on business expansion. Moreover, now each individual has smart phone which contain several
        games and other applications. This aspect has developed high level interest of each and every individual
        and thereby made contribution in the growth as well as development of such gaming industry (Castro
        Soeiro and et.al., 2016). Now, with the motive to spend time in joyful manner individuals play game on
        their mobile phones. Pokémon go game is one of the biggest examples of video game which has generated
        high level of popularity. Each individual who has smart phone encouraged to download and play such game.
        In this, people have task to catch Pokémon which are present at different place. In this way, interest
        level of individuals was developed significantly in the recent past. By considering such aspect it can
        be said that smart phone and tablets encourage more people to play game.
      </p>
      <h5>Research questions
      </h5>
      <p>In the present study, research question is focusing on the extent to which tablet and smart phone devices
        encourages everyone to play game. Hence, from investigation it has been assessed that use of mobile
        phones in plying games are increased with the very high pace. Moreover, individual can play game from
        every place which in turn also motivates them towards this. Further, now gaming companies design battery
        saver games with the motive to involve everyone (Castañeda and Cho, 2016). In addition to this, video
        games build and enhance skills, competency and proficiency level of personnel to a great extent. Videos
        games are repeatedly played by people develop their patience and perseverance level. Tablet and smart
        phone users play their favorite game repeatedly. This in turn contributes in their growth and
        development aspect. Role playing games such as Final Fantasy, Dragon Warrior etc. helps people in making
        stronger. Along with this, there are several video games are available which can be played by people on
        their phones such as plants vs Zombies, angry birds etc. All these games develop ability of individuals
        in relation to forward thinking and strategic planning (Carvalho and Araújo, 2016). Thus, it is another
        main aspect which influences people to play game and thereby enhance potential.
      </p>
      <p>Along with this, friendship groups are also encouraged to play games with each other who have smart
        phones. In this way, such games maximize leadership and socialization to the significant level. Now,
        there are several games which enable people to spend their time with each other. Besides this, there are
        several online games available to individual who offer opportunity to people to communication with each
        other through voice and video chat. In addition, to develop mental and creative prowess tablets and
        mobile phone users are influenced to play game (Ebner and et.al., 2016). Sudoku, Tetris etc. games
        improve brain functionality of people to the significant level. When, people start to play specific game
        then their desire in relation to cross each and every step increased with the very high pace (Skill
        development through video games, 2017). In this way, by solving all the logical deductions individual
        can enhance mental capabilities to the significant level.
      </p>
      <p>Further, Tetris game helps in enhancing pattern recognition skills and facilitates mental exercise.
        Hence, such game enables individual to think outside the box and thereby fulfill their objectives. In
        addition to this, all the above mentioned games do not take more space of phone memory and thereby
        encourage each people to play game. All above presented trend shows that with the rise in the usage of
        tablets and smart phones involvement of people in playing mobile based games are increased significantly
        (Leaver and Willson, 2016). It also provides convenience to them to the large extent. Thus, by keeping
        all such aspects in mind it can be said that tablet and mobile phone influences everyone to play game
        and thereby enhances their skills and potentials.
      </p>
      <h4>CONCLUSION
      </h4>
      <p>From the above research project, it has been concluded that by tablets and mobile phone have placed high
        level of impact on the involvement level of individuals. Besides this, it can be revealed from the
        report that such mobile based games make significant improvement in skills and competency level of
        personnel. Thus, for fulfilling self-interest and development aspect individual places emphasis on
        playing mobile games to the large extent.
      </p>
      <h5>References
      </h5>
      <ul>
        <li><p>Aiyar, S., Calomiris, C.W. and Wieladek, T., 2014. Does Macro‐Prudential Regulation Leak? Evidence
            from a UK Policy Experiment. Journal of Money, Credit and Banking.46(s1). pp.181-214.</p>
        </li>
        <li><p>Carpenter, J.R., Roger, J.H. and Kenward, M.G., 2013. Analysis of longitudinal trials with protocol
            deviation: a framework for relevant, accessible assumptions, and inference via multiple imputation.
            Journal of biopharmaceutical statistics.23(6). pp.1352-1371.</p>
        </li>
        <li><p>Dalla Chiara, B., Cornaglia, L. and Deflorio, F., 2016. Macro-analysis of the evolution of motorised
            mobility and relationships with the development of motionless communication systems. IET Intelligent
            Transport Systems.10(9). pp.613-621.</p>
        </li>
        <li><p>&gt;Di Mauro, F. and Pesaran, M.H. eds., 2013. The GVAR handbook: structure and applications of a macro
          model of the global economy for policy analysis. OUP Oxford.</p ></li>
          <li><p>Huang, R., 2015. China: A macro history. Routledge.</p>
          </li>
          <li><p>Kadam, P.V. and Sudarsan, P.K., 2016. Competitiveness of India’s Software Industry: A Macro Level
              Analysis. Journal of International Economics. 7(1). pp.17.</p>
          </li>
        </ul>
    </div>
  </div>
  <Footer/>
</div>

    )
}
