import React from 'react'
import Header from './components/Header'
import Footer from './components/Footer'

const TermConditions = () => {

    return (
<div>
    <Header/>
  <br />
  <br />
  <br />
  <div className="container">
    <h3>Terms and Conditions
    </h3>
    <p>Thank You for visiting "The Assignment Writing" and also for selecting us to satisfy your educational needs.
      But you are highly recommended to read this paper thoroughly and agree our conditions prior using our
      scholarly services. If you do not agree with our particular conditions, do not provide any order to us.
    </p>
    <p>Ordering any assignment, document on this website indicates that you agree all the terms and conditions
      discussed on this page.
    </p>
    <h3>1. Vocabulary 
    </h3>
    <ul>
      <li>"Website” means "theassignmentwriting.com"
      </li>
      <li>"You,” “Yours” or “Customer” resemble you or any other person
      </li>
      <li>“Writer” means the person who has agreed to finish your assignment
      </li>
      <li>“We,” “Our” or “Company” means theassignmentwriting.com registered under the laws
      </li>
      <li>“Order” means an electronic request paid for a specific product or service
      </li>
      <li>“Quality Assurance Team” means a department under the company's organizational structure to deliver high-quality output 
      </li>
    </ul>
    <h3>2. Use of our Services 
    </h3>
    <ul>
      <li>Our company has professional and experienced academic writers, editors, researchers and proofreaders for the delivery of exceptional assignment writing service.
      </li>
      <li>The customer's agreement with us will be effective instantly when the payment is received, and finished order is delivered.
      </li>
      <li>Once the payment is accepted from you, our services will be responsible to produce the customers with the standard quality as directed.
      </li>
      <li>Clients can file the grievances within seven days to us if we fail to fulfill the client's expectations.
      </li>
      <li>Clients are advised to view our refund policy prior to requesting for the reimbursement.
      </li>
      <li>The completed order will be addressed to the email address without any extra costs.
      </li>
      <li>The company guarantees to assist its clients 24/7 with their experienced customer service representatives.
      </li>
      <li>We agree to assign qualified and skilled writers to create authentic and genuine documents.
      </li>
      <li>Any unauthorized use of the delivered product or content of the website can subject our clients to criminal or civil penalties. 
      </li>
    </ul>
    <h3>3. Delivery Policy
    </h3>
    <p>The Assignment Writing is responsible for rendering the on-time delivery of ordered papers to its clients. In case, we fail to present the project on-time, then the client can ask for a total refund. We produce the documents according to the required specifications and standards. We guarantee the delivery of standard quality contents to our customers.
    </p>
    <h3>4. No Plagiarism
    </h3>
    <p> By agreeing to our conditions, you confirm that we reserve the right to cancel any arrangement, agreement, or contract, with any individual who attempts to supply plagiarized products as original.
    </p>
    <ul>
      <li>You also agree not to share any product presented by us to the third party.
      </li>
      <li>You also agree not to share any product presented by us to the third party.
      </li>
      <li>We reserve the right to decline to carry out your work and services if we find that the client has used the given product in the form of plagiarism.
      </li>
      <li>All the documents we offer in the form of written materials should be used for research or reference purposes only.
      </li>
    </ul>
    <h3>5. Data Protection and Security
    </h3>
    <p>We will never disclose your personally identifiable information (PII) other than as demanded to do by the legal agency. Please read our privacy policy page that can be found on our website. You can collect detailed knowledge of our policies regarding data security.
    </p>
    <p>Please view our policies section to get more knowledge about the security of personal information.
    </p>
    <h3>6. Warranties
    </h3>
    <p>By placing an order, you acknowledge that you completely agree with the above-stated points, as well as with the following points:
    </p>
    <ul>
      <li>All information used from the provided paper must be properly cited.
      </li>
      <li>All products we deliver should be used only as examples for reference or research. You must use for the purposes of learning the techniques for writing a paper in a right format and the correct citation style (MLA, APA, Chicago, Turabian, Harvard, etc.).
      </li>
      <li>No copies of the product will be distributed, and you agree to destroy the product immediately after your reference or research use.
      </li>
      <li>You agree to receive the latest information regarding offers or contests of the company. You can unsubscribe these updates as when required.
      </li>
    </ul>
    <h3>7. Amendments in Term of Service
    </h3>
    <ul>
      <li>The company possesses the authorization to change or modify these Terms and Conditions unilaterally.
      </li>
      <li>We advise that our clients keep any eye on any modifications made to terms mentioned above on a daily basis.
      </li>
      <li>It's our duty to reflect the modifications immediately on the web pages.
      </li>
    </ul>
  </div>
  <Footer/>
</div>

    )
}



export default TermConditions

























