import React from 'react'
import Header from './components/Header'
import Footer from './components/Footer'
export default function RefundPolicy() {
    return (
<div>
    <Header/>
  <br />
  <br />
  <br />
  <div className="container">
    <h3>Refund Policy
    </h3>
    <h3>Find Every Data related to Our Refund Policy:
    </h3>
    <p>Here, at The Assignment Writing, we endeavour to provide excellence in every regard of our services.
      Sometimes a minute error is possible by the company due to numerous reasons and fails to satisfy your
      expectations. For this deduction, if you are totally dissatisfied with our work, we are willing to
      compensate our clients by providing them a refund.
    </p>
    <h4>Before lodging a refund request, we request our customers to read the following carefully:
    </h4>
    <p>If a client is not satisfied with the work even after having seven revisions submitted by us, then the
      customer is entitled to file a complaint by communicating our customer support executives. After that it is
      our responsibility to assign your order to an experienced writer to complete your order successfully.
    </p>
    <h4>Get entire refund in following cases:
    </h4>
    <ul>
      <li>
        <p>If you are charged two times by credit card accidentally, The Assignment Writing will be liable to
          return the mistakenly charged amount immediately.</p>
      </li>
      <li><p>When we miss agreed submission deadline by more than 24 hours.</p>
      </li>
      <li><p>If the plagiarism percentage is more than University guidelines during the final submission.</p>
      </li>
      <li><p>If you fail to inform us prior communicating to a third party, it will be recognized as a breach of
          contract.</p>
      </li>
    </ul>
    <h4>Important Note:
    </h4>
    <p>If you wish to get a complete return, then the refund request can be made only within seven days of receiving
      your order. The Quality Assurance Department occupy all rights to make a final decision respecting a refund
      by considering all the evidence.
    </p>
  </div>
  <Footer/>
</div>

    )
}
