import React from 'react'
import Header from './components/Header'
import Footer from './components/Footer'

export default function FAQs() {
    return (
       <div>
           <Header/>
  {/*hero section start*/}
  <section className="hero-section ptb-100 FQ" >
    <div className="container">
      <div className="row align-items-center justify-content-center">
        <div className="col-md-9 col-lg-7">
          <div className="page-header-content text-white text-center pt-sm-5 pt-md-5 pt-lg-0">
            <h2 className="text-white mb-0">FAQ
            </h2>
            <p><i className="fa fa-leaf text-success" /><b>Free Tumitin Report</b></p>
            <p><i className="fa fa-flag text-success" /><b>7,00000+ Total Satisfied Student</b></p>
            <p><i className="fa fa-thumbs-up text-primary" /><b>150+ Executive for Support Service</b></p>
          </div>
        </div>
      </div>
    </div>
  </section>
  {/*hero section end*/}
  {/* start */}
  
  <section className='Faq-container'>
  <br />
  <br />
  <br />
  <div className="container ">
    <h4 className="ico">Frequently Asked Questions</h4>
    <h4 className=" ico">Q: What is the procedure to place an order?</h4>
    <p>You have to fill in the online order form with every required field. After it, click on ‘Get Price’
      button to see our pricing structure. Proceed with your selection of price and standard for the table of
      prices. We would also like to bring it to your notice that pricing for a few of the orders depends on
      the nature of your customized requirements; therefore it won’t be possible to provide you with the exact
      cost of your paper immediately. For instance, to fulfil editing, modification, proofreading or any ad
      hoc requirement, we have to discuss the pricing with our writers. Next step is to fill in the
      requirements section and proceed to the payment page. The order process completes when you select a
      method to pay for our services and make the payment. Once the payment is made, we will start working on
      your project.</p>
    <h4 className=" ico">Q: Are your custom written papers committed to originality?</h4>
    <p>Yes, we provide guaranteed original work, and we never resell or republish the papers written especially
      for you.</p>
    <h4 className=" ico">Q: What is the pricing structure to order an assignment?</h4>
    <p>Our prices will be available only when you complete the order form mentioning all your specifications and
      proceed to know the pricing structure.</p>
    <h4 className=" ico">Q: Which subjects do you cover for assignment writing?</h4>
    <p>We offer comprehensive academic help for a broad range of subjects at all academic levels, including high
      school, graduation, post graduation, and doctorate level. The order form page has a menu that depicts
      our specialization, choose your subject from the list or enter manually to get our services.</p>
    <h4 className=" ico">Q: How long will I wait to get my order done?</h4>
    <p>Turn around time for each academic paper varies from one day to one month, depending upon the kind of
      document requested as well as on the availability of the expert. You can select the urgency while
      filling in the order form; prices may vary depending upon how urgently you need to get the assignment
      delivered.</p>
    <h4 className=" ico">Q: What guarantees will I get as a customer?</h4>
    <p>To ensure you that you have selected the best service provider in the industry, here is the list of
      guarantees which you get:</p>
    <ol>
      <li><p>100% satisfaction guarantee</p></li>
      <li><p>100% plagiarism free content guarantee</p></li>
      <li><p>100% money back guarantee</p></li>
      <li><p>Promised quality standard of 1st, 2:1, 2:2</p></li>
      <li><p>Free of cost modifications within the given time slot</p></li>
      <li><p>On-time delivery guarantee</p></li>
      <li><p>Committed to maintaining confidentiality</p></li>
      <li><p>No resell or publishing of any delivered content</p></li>
    </ol>
    <h4 className=" ico">Q: What are the details required by the writer to complete my order?</h4>
    <p>You are supposed to provide requirements related to your project, in addition to the extra details such
      as assessment criteria, additional resources such as case studies, handbooks, lecture notes, etc.</p>
    <h2 className=" ico">Q: Is there any option to reference a specific book or some other resource?</h2>
    <p>Yes, when you complete the order form, you can fill in the menu of specific sources to provide us with
      the reference. We may ask for an electronic variant or scanned copy of your reference.</p>
    <h4 className=" ico">Q: What is the delivery process for the completed order?</h4>
    <p>You will get your order via email, before or on the day of your defined deadline. All our work will be
      delivered in MS Word format; hard copies and fax can be provided for an extra charge.</p>
    <h4 className=" ico">Q: Do I get calculation services with my order?</h4>
    <p>Yes, our writers can calculate to do your assignment. When you tick the calculation option in the order
      form, our writers will calculate any equation related to formulas, ratios, profit &amp; loss and financial
      analysis. You will be charged with extra fee for availing calculating service.</p>
    <h4 className=" ico">Q: What are the quality standards guaranteed by you?</h4>
    <p>Our assignments are based on the three levels of quality standard, which are:</p>
    <ul>
      <li><p>1st Quality Standard: Equivalent to A Grade.</p></li>
      <li><p>2:1Quality Standard: Equivalent to B Grade.</p></li>
      <li><p>2:2 Quality Standard: Equivalent to C Grade.</p></li>
    </ul>
    <h4 className=" ico">Q: Can you guarantee plagiarism-free academic work?</h4>
    <p>Yes, we offer plagiarism-free academic work to our valued customers. All work done by our experts come
      with 100% money back guarantee, so you can claim money back in case the originality of the submitted
      work is found below the set university standards. </p>
    <h4 className=" ico">Q: What procedure should I follow to submit my paper?</h4>
    <p>You can submit the work done by our writers, only if you follow the recommended policy of use, provided
      on our website. </p>
    <h4 className=" ico">Q: Can I use the content provided by you?</h4>
    <p>You can use it as a study aid only. If you want to use it for research purpose, then make sure to
      reference it properly. For more details, refer to our recommended policy of use.</p>
    <h4 className=" ico">Q: Is ordering from The Assignment Writing safe? </h4>
    <p>Yes, it’s absolutely safe to get your papers done with the help of our writers. Our service is 100%
      confidential; we will never share your details with any third party. To make sure that your details will
      remain private, we remove your personal data from any attachment that you send to us for reference
      purpose. </p>
    <h4 className=" ico">Q: How is Assignment Help different from other service providers?</h4>
    <p>No, instead our work will serve you as an aid to foster your academic development. Though you have to be
      sensible enough to use our services for journal publication and books. Using our work is 100% legal, so
      be assured to get the academic quality document you are looking for.</p>
    <h4 className=" ico">Q: What are the services provided by your team?</h4>
    <p className="row-last">Our services range from writing custom essays, assignments to complex papers such as reflexive reports.
      We also provide editing, proofreading, and SPSS services to our clients.</p>
  </div>
  </section>
  {/* end */}<br />
  <Footer/>
</div>

    )
}
